.dragdroptable {
    width: 80%;
    /* display: flex;   */
    justify-content: center;
    align-items: center;
    padding: 20px;
    background-color: rgb(255, 255, 255);
    border-radius: 5px;
    box-shadow: 0px 7px 6px #00000029;
    padding-left: 10%;
    padding-right: 10%;
    text-align: center;
  }

  .prefilled {
    width: 110px;
    height: 30px;
    justify-content: center;
    padding: 5px;
    background-color: #b3def7;
    border: 0px;
    border-radius: 5px;
    box-shadow: 0px 7px 6px #00000029;
    padding-left: 20px;
    padding-right: 20px;
    margin: 8px;
  }


  .tblcellcolor{
    background-color: #F5D38B;
    width: 110px;
    height: 30px;
    /* justify-content: center;
    align-items: center; */
    text-align: center;
    background: #F5D38B 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 5px;
    opacity: 1;
    color: #412410;
    padding: 5px;
    margin: 5px;
    border: 0px;

  }

  .sentence1 {
    width: 110px;
    height: 30px;
    justify-content: center;
    padding: 5px;
    background-color:#FFFFFF;
    border: 0px;
    border-radius: 5px;
    box-shadow: 0px 7px 6px #00000029;
    padding-left: 20px;
    padding-right: 20px;
    margin: 8px;
  }

  .sentence2{
    width: 125px;
    height: 30px;
    justify-content: center;
    padding: 5px;
    background-color: #FFFFFF;
    border: 0px;
    border-radius: 5px;
    box-shadow: 0px 7px 6px #00000029;
    padding-left: 20px;
    padding-right: 20px;
    margin: 8px;
  }

  .imginfo{
    height: 15px;
    width: 15px;
    margin-left: 3px;
  }

  .tblcellincorrect{
    background-color: #E74C3C;
    width: 110px;
    height: 30px;
    border-radius: 5px;
    box-shadow: inset 0px 3px 6px #00000029;
    padding-left: 20px;
    padding-right: 20px;
    border: 0px;
    margin: 8px;
  }

  .tblcellcorrect{
    background-color: #58D68D;
    width: 110px;
    height: 30px;
    border: 0px;
    border-radius: 5px;
    padding-left: 20px;
    padding-right: 20px;
    box-shadow: inset 0px 3px 6px #00000029;
    padding-top: 0px;
    margin: 8px;
  }

  .tblcellincorrect2{
    background-color: #E74C3C;
    width: 125px;
    height: 30px;
    border-radius: 5px;
    box-shadow: inset 0px 3px 6px #00000029;
    padding-left: 20px;
    padding-right: 20px;
    border: 0px;
    margin: 8px;
  }

  .tblcellcorrect2{
    background-color: #58D68D;
