    height: 30px;
    border: 0px;
    border-radius: 5px;
    padding-left: 20px;
    padding-right: 20px;
    box-shadow: inset 0px 3px 6px #00000029;
    padding-top: 0px;
    margin: 8px;
  }

  .btnsubmit{
    width: 130px;
    height: 35px;
    background: #d5acd5 0% 0% no-repeat padding-box;
    border-radius: 5px;
    opacity: 1;
    color: #4E0870;
    border: 0px solid #202020;
    box-shadow: 0px 7px 6px #00000029;
    font-weight: bold;
    font-size: 14px;
    margin-right: 10px;
  }

  .btnclear{
    width: 130px;
    height: 35px;
    background: #d5acd5 0% 0% no-repeat padding-box;
    border-radius: 5px;
    opacity: 1;
    color: #4E0870;
    border: 0px solid #1b1a1a;
    box-shadow: 0px 7px 6px #00000029;
    font-weight: bold;
    font-size: 14px;
    margin-right: 10px;
  }


  .btnnext{
    width: 130px;
    height: 35px;
    background: #d5acd5 0% 0% no-repeat padding-box;
    border-radius: 5px;
    opacity: 1;
    color: #4E0870;
    border: 0px solid #1b1a1a;
    box-shadow: 0px 7px 6px #00000029;
    font-weight: bold;
    font-size: 14px;
    margin-right: 10px;
  }

  .btnshowanswer{
    width: 130px;
    height: 35px;
    background: #d5acd5 0% 0% no-repeat padding-box;
    border-radius: 5px;
    opacity: 1;
    border: 0px solid #1b1a1a;
    box-shadow: 0px 7px 6px #00000029;
    font-weight: bold;
    font-size: 14px;
    margin-right: 10px;
    color: #4E0870;
  }

  .wordpool{
    justify-content: center;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    background-color:#FDF6E7;
    padding: 10px;
    border-radius: 5px;
  }

  .btnpanel{
    margin-top: "30px";
    margin-bottom: "10px";
  }

